    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --back-blur-header: 12px;
  --box-shadow-default: 0 4px 6px -2px rgba(0 0 0 / 5%), 0 10px 15px -3px rgba(0 0 0 / 10%);
  --white-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0) 0, var(--white) 100%);
