  --z-index-header: 2;
  --white: #ffffff;
  --white-transparent: rgba(255 255 255 / 95%);
  --zinc-100: #f4f4f5;
  --zinc-200: #e4e4e7;
  --zinc-300: #d4d4d8;
  --zinc-400: #a1a1aa;
  --zinc-700: #3f3f46;
  --orange-500: #f97316;
  --orange-600: #ea580c;
  --purple-300: #d8b4fe;
  --purple-700: #7e22ce;
  --vk-brand-color: #4680c2;
  --vk-brand-hover: #22558e;
  --front-primary: var(--zinc-700);
  --front-invert: var(--white);
  --back-primary: var(--white);
  --header-height: 140px;
}
@media screen and (max-width: 1279px) {
  :root {
    --header-height: 160px;
  }
}
@media screen and (max-width: 991px) {
  :root {
    --header-height: 140px;
  }
}
