  font-size: 15px;
  line-height: 1.3334;
  margin: 0;
}
.accordion__body p + p {
  margin-top: 10px;
}
.accordion__body a {
  color: var(--purple-700);
  font-weight: 500;
  text-decoration-line: underline;
}
@media (hover: hover) {
  .accordion__body a:hover {
    text-decoration: none;
  }
}
.accordion__video {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 25px;
}
.accordion__video-bg {
  display: block;
  width: 100%;
  height: auto;
}
