  --vertical-shift: calc(var(--stroke-width) / 2 * -1);
  position: absolute;
  top: 50%;
  display: block;
  width: calc(100% - var(--stroke-width));
  height: 1px;
  margin-right: var(--stroke-width);
  border-top: var(--stroke-width) solid currentColor;
  transform: translateY(var(--vertical-shift));
  content: "";
}
